/* stylelint-disable-next-line at-rule-no-unknown */
@tailwind base;
/* stylelint-disable-next-line at-rule-no-unknown */
@tailwind components;
/* stylelint-disable-next-line at-rule-no-unknown */
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', system-ui, sans-serif;
  }
  
  body {
    @apply bg-gray-50 text-gray-900;
  }
}

@layer components {
  .btn-primary {
    @apply bg-blue-600 text-white px-6 py-3 rounded-lg font-semibold hover:bg-blue-700 transition-colors duration-200;
  }
  
  .btn-secondary {
    @apply bg-gray-200 text-gray-800 px-6 py-3 rounded-lg font-semibold hover:bg-gray-300 transition-colors duration-200;
  }
  
  .card {
    @apply bg-white rounded-xl shadow-lg p-6 transition-shadow duration-200 hover:shadow-xl;
  }
  
  .idea-card {
    @apply bg-white rounded-xl shadow-md hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1 cursor-pointer;
  }
  
  .filter-chip {
    @apply px-4 py-2 bg-blue-100 text-blue-800 rounded-full text-sm font-medium hover:bg-blue-200 transition-colors cursor-pointer;
  }
  
  .filter-chip.active {
    @apply bg-blue-900 text-white hover:bg-blue-800;
  }

  .hero-gradient {
    background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  }

  .category-icon {
    @apply w-16 h-16 rounded-full flex items-center justify-center transition-transform duration-200 hover:scale-110;
  }

  .nav-link {
    @apply text-gray-600 hover:text-blue-900 transition-colors duration-200;
  }

  .nav-link.active {
    @apply text-blue-900 border-b-2 border-yellow-400 pb-1;
  }
} 

/* Custom animations for the 3D rocket */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes rocket-fly {
  0% {
    transform: rotate(12deg) translateY(20px) translateX(-10px);
  }
  50% {
    transform: rotate(12deg) translateY(-10px) translateX(5px);
  }
  100% {
    transform: rotate(12deg) translateY(20px) translateX(-10px);
  }
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

.animate-rocket-fly {
  animation: rocket-fly 4s ease-in-out infinite;
} 